<template>
	<section>
		<div class="item_bundle" v-if="isAd">
			<strong class="tit_bundle"></strong>
			<div class="cont_bundle">
				<div class="box_reject_ad">
					<div class="box_add_radio">
						<span class="custom_radio">
							<input type="radio" name="radioAd" id="hiddenAd" v-model="selectType" value="">
							<label for="hiddenAd"></label>
						</span>
						<span class="custom_radio">
							<input type="radio" name="radioAd" id="visibleAd" v-model="selectType" value="">
							<label for="visibleAd"></label>
						</span>
					</div>
					<div class="box_add_input">
						<strong class="title"></strong>
						<span class="custom_input">
							<input type="text">
						</span>
					</div>
				</div>
			</div>
		</div>

		<div class="item_bundle">
			<strong class="tit_bundle"></strong>
			<div class="cont_bundle">
				<div class="box_reject_ad">
					<div class="box_add_radio">
						<span class="custom_radio">
							<input type="radio" name="radioCa" id="hiddenCa" v-model="selectType2" value="">
							<label for="hiddenCa"></label>
						</span>
						<span class="custom_radio">
							<input type="radio" name="radioCa" id="visibleCa" v-model="selectType2" value="">
							<label for="visibleCa"></label>
						</span>
						<p class="caution">      . </p>
					</div>
				</div>
			</div>
		</div>

		<div class="item_bundle">
			<strong class="tit_bundle">Fallback</strong>
			<div class="cont_bundle">
				<div class="cont_block_bundle fallback_raido">
					<span class="custom_radio">
						<input type="radio" name="selectSand" id="notUsed" checked>
						<label for="notUsed" v-on:click="fallBackRadio($event)" data-id="0"></label>
					</span>
					<span class="custom_radio">
						<input type="radio" name="selectSand" id="sendSms">
						<label for="sendSms" v-on:click="fallBackRadio($event)" data-id="1">SMS</label>
					</span>
					<span class="custom_radio">
						<input type="radio" name="selectSand" id="sendLms">
						<label for="sendLms" v-on:click="fallBackRadio($event)" data-id="2">LMS</label>
					</span>
				</div>
				<div class="cont_block_bundle box_textarea active"></div>
				<div class="cont_block_bundle box_textarea">
					<div class="custom_textarea">
						<textarea placeholder="    ( 90byte)"></textarea>
					</div>
					<!-- 2019.09.18 tooltip  -->
					<div class="cont_flex_bundle bottom">
						<p class="caution">  90bytes/00 bytes 
						<div class="tooltip_area">
							<div class="btn_info"></div>
							<div class="box_tooltip">
								<p> 1=2byte, <br>  /// 1=1byte  .</p>
							</div>
						</div>
						</p>
					</div>
					<p class="caution"> RCS     SMS LMS   . </p>
					<p class="caution">     {{ blank }}  . <br>) {{ customerName }}
						{{ numDate }}.</p>
				</div>
				<div class="cont_block_bundle box_textarea">
					<!-- 2020.03.05 custom_input   -->
					<span class="custom_input full_width mar_b10">
						<input type="text" placeholder="    ( 40byte)">
					</span>
					<div class="custom_textarea">
						<textarea placeholder="    ( 2000byte)" v-model="LMSContText"
							@input="LMSContText = $event.target.value"></textarea>
					</div>
					<!-- 2019.09.18 tooltip  -->
					<div class="cont_flex_bundle bottom">
						<p class="caution">  2000bytes/00 bytes 
						<div class="tooltip_area">
							<div class="btn_info"></div>
							<div class="box_tooltip">
								<p> 1=2byte, <br>  /// 1=1byte  .</p>
							</div>
						</div>
						</p>
					</div>
					<p class="caution"> RCS     SMS LMS   . </p>
					<p class="caution">             .</p>
				</div>
			</div>
		</div>


		<div class="item_bundle">
			<strong class="tit_bundle">ID</strong>
			<div class="cont_bundle">
				<span class="custom_input full_width">
					<input type="text" placeholder="ID .">
				</span>
				<p class="caution">    ID  .</p>
				<p class="caution"> &#44; &#44; ( &#46;  &#44;  &#45;  &#44;  &#64; )  .
				</p>
			</div>
		</div> <!-- 20.08.14  -->
	</section>
</template>

<script>

export default {
	name: 'fallbackSetBox',
	props: {
		isAd: {
			type: Boolean,
			default: false,
		},
	},
	data() {
		return {
			SMSContText: '',
			LMSContText: '',
			customerName: '{{}}',
			numDate: '{{YYYYMMDD}}',
			blank: '{{ }}',
			selectType: '',
			selectType2: '',
		};
	},
	components: {

	},
	methods: {
		fallBackRadio: function (event) {
			var myIndex = event.target.getAttribute('data-id');
			var myTabContent = document.querySelectorAll('.cont_block_bundle.box_textarea');
			for (var i = 0; i < myTabContent.length; i++) {
				myTabContent[i].classList.remove('active');
			}
			myTabContent[myIndex].classList.add('active');
		},
	},
	computed: {

	}
};
</script>